		}
	}
	
	image {
		width: 100%;
		height: 100%;
		display: block;
	}

	::-webkit-scrollbar {
		width: 0;
		height: 0;
		color: transparent;
	}
	
	// 顶部真正1px线
	.lineTop:after{
		position: absolute;
