		width: 100%;
		left: 0;
		top: 0;
		height: 1px;
		background-color: #f5f5f5;
		transform: scale(1, 0.5);
		transform-origin: center bottom;
	}
	
	// 底部真正1px线
	.lineBottom:after{
		position: absolute;
		content: '';
		width: 100%;
		left: 0;
		bottom: 0;
		height: 1px;
		background-color: #f5f5f5;
		transform: scale(1, 0.5);
		transform-origin: center bottom;
	}
	
	.loadingBox{
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: 999;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #fff;
